'use server'

import { createClient } from '@/lib/supabase/server'
import { getServerSession } from '@/lib/supabase/auth-helpers'

/**
 * Response type for server actions
 */
export interface UserManagementResponse {
  success: boolean
  data?: any
  error?: string
}

/**
 * Get all users (Admin only)
 * Returns list of all registered users with their details
 */
export async function getAllUsers(): Promise<UserManagementResponse> {
  try {
    // Create client first to check what cookies are available
    const supabase = await createClient()
    
    // Check what cookies are available
    const cookieStore = await import('next/headers').then(m => m.cookies())
    const allCookies = cookieStore.getAll()
    const supabaseCookies = allCookies.filter(c => c.name.includes('sb-') || c.name.includes('supabase'))
    console.log('[getAllUsers] Available Supabase cookies:', supabaseCookies.map(c => c.name).join(', ') || 'None')
    console.log('[getAllUsers] Total cookies:', allCookies.length)
    
    // Try multiple methods to get the user
    console.log('[getAllUsers] Attempting to get user session...')
    
    // Method 1: Try getSession (reads from cookies)
    const { data: { session }, error: sessionError } = await supabase.auth.getSession()
    
    if (sessionError) {
      console.error('[getAllUsers] getSession() error:', sessionError)
    }
    
    let authUser = session?.user
    
    // Method 2: If no session, try getUser() (reads from headers/tokens)
    if (!authUser) {
      console.warn('[getAllUsers] No session from getSession(), trying getUser()...')
      const { data: { user }, error: userError } = await supabase.auth.getUser()
      
      if (userError) {
        console.error('[getAllUsers] getUser() error:', {
          message: userError.message,
          status: userError.status,
        })
      }
      
      authUser = user || null
    }
    
    if (!authUser) {
      console.error('[getAllUsers] No authenticated user found')
      return { 
        success: false, 
        error: 'Unauthorized: Please log in. If you just logged in, try refreshing the page or logging out and back in.' 
      }
    }
    
    console.log('[getAllUsers] User authenticated:', authUser.email)
    
    // Get user profile to check admin status
    const { data: profile, error: profileError } = await supabase
      .from('users')
      .select('id, email, role')
      .eq('id', authUser.id)
      .maybeSingle()

    if (profileError) {
      console.error('[getAllUsers] Error fetching user profile:', {
        message: profileError.message,
        code: profileError.code,
        details: profileError.details,
      })
      return { success: false, error: `Profile error: ${profileError.message}` }
    }

    if (!profile) {
      console.error('[getAllUsers] User profile not found:', authUser.id)
      return { success: false, error: 'User profile not found' }
    }

    // Verify user is admin
    if (profile.role !== 'admin') {
      console.warn('[getAllUsers] Non-admin user attempted access:', profile.email)
      return { success: false, error: 'Unauthorized: Admin access required' }
    }
    
    console.log('[getAllUsers] Admin verified, fetching all users...')
    const { data: users, error } = await supabase
      .from('users')
      .select(`
        id,
        email,
        role,
        first_name,
        last_name,
        phone,
        approved,
        created_at,
        last_login
      `)
      .order('created_at', { ascending: false })

    if (error) {
      console.error('Error fetching users list:', {
        message: error.message,
        code: error.code,
        details: error.details,
        hint: error.hint,
      })
      return { 
        success: false, 
        error: error.message || 'Failed to fetch users',
        // Include error code for debugging
        ...(error.code && { errorCode: error.code })
      }
    }

    console.log(`Successfully fetched ${users?.length || 0} users`)
    return { success: true, data: users || [] }
  } catch (error: any) {
    console.error('Unexpected error in getAllUsers:', {
      message: error.message,
      stack: error.stack,
      name: error.name,
    })
    
    // Handle redirect errors from requireAdmin if it was used elsewhere
    if (error.message?.includes('redirect') || error.message?.includes('NEXT_REDIRECT')) {
      return { success: false, error: 'Unauthorized: Admin access required' }
    }
    
    return { success: false, error: error.message || 'Failed to fetch users' }
  }
}

/**
 * Get user by ID (Admin only)
 */
export async function getUserById(userId: string): Promise<UserManagementResponse> {
  try {
    // Create client and verify admin status
    const supabase = await createClient()
    
    const { data: { user: authUser }, error: authError } = await supabase.auth.getUser()
    if (authError || !authUser) {
      return { success: false, error: 'Unauthorized: Please log in' }
    }

    const { data: profile } = await supabase
      .from('users')
      .select('role')
      .eq('id', authUser.id)
      .maybeSingle()

    if (!profile || profile.role !== 'admin') {
      return { success: false, error: 'Unauthorized: Admin access required' }
    }

    const { data: user, error } = await supabase
      .from('users')
      .select(`
        id,
        email,
        role,
        first_name,
        last_name,
        phone,
        approved,
        created_at,
        last_login
      `)
      .eq('id', userId)
      .maybeSingle()

    if (error) {
      console.error('Error fetching user:', error)
      return { success: false, error: error.message }
    }

    if (!user) {
      return { success: false, error: 'User not found' }
    }

    return { success: true, data: user }
  } catch (error: any) {
    console.error('Error in getUserById:', error)
    return { success: false, error: error.message || 'Failed to fetch user' }
  }
}

/**
 * Approve user (Admin only)
 * Sets approved = true for the specified user
 */
export async function approveUser(userId: string): Promise<UserManagementResponse> {
  try {
    // Create client and verify admin status
    const supabase = await createClient()
    
    const { data: { user: authUser }, error: authError } = await supabase.auth.getUser()
    if (authError || !authUser) {
      return { success: false, error: 'Unauthorized: Please log in' }
    }

    const { data: profile } = await supabase
      .from('users')
      .select('role')
      .eq('id', authUser.id)
      .maybeSingle()

    if (!profile || profile.role !== 'admin') {
      return { success: false, error: 'Unauthorized: Admin access required' }
    }

    // First verify user exists
    const { data: existingUser, error: fetchError } = await supabase
      .from('users')
      .select('id, email, approved')
      .eq('id', userId)
      .maybeSingle()

    if (fetchError) {
      console.error('Error fetching user:', fetchError)
      return { success: false, error: fetchError.message }
    }

    if (!existingUser) {
      return { success: false, error: 'User not found' }
    }

    if (existingUser.approved) {
      return { success: false, error: 'User is already approved' }
    }

    // Update user approval status
    const { data: updatedUser, error: updateError } = await supabase
      .from('users')
      .update({ approved: true })
      .eq('id', userId)
      .select(`
        id,
        email,
        role,
        first_name,
        last_name,
        phone,
        approved,
        created_at,
        last_login
      `)
      .single()

    if (updateError) {
      console.error('Error approving user:', updateError)
      return { success: false, error: updateError.message }
    }

    return { 
      success: true, 
      data: updatedUser,
      error: undefined 
    }
  } catch (error: any) {
    console.error('Error in approveUser:', error)
    return { success: false, error: error.message || 'Failed to approve user' }
  }
}

